/*homepage profile image*/
section {
  width: 20%;
  margin: 0 auto;
  line-height: 0;
  position: relative;
}
section img {
  width: 100%;
  -webkit-filter: grayscale(.3);
          filter: grayscale(.3);
}
section article {
  position: absolute;
  top: 0;
  width: 100%;
  background: #66ccff;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 0.95vw;
  mix-blend-mode: lighten;
}
section div {
  background: black;
  height: 6vw;
}
section div{
  grid-column: 1/1/1/1;
  grid-row: 1/1/1/1;
}
section div:nth-of-type(10n), section div:nth-of-type(3){
	background: #66ccff;
}

@media screen and (max-width: 768px){
	section {
	  width: 290px;
	}
	section article {
		grid-gap: 14px;
	}
	section div {
		height: 87px;
	}
}

/*homepage buttons*/
.homeButtonGroup{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	margin: 15px auto;
}
.roundButton{
	width: 20px;
	height: 20px;
	border-radius: 30px;
	margin: 0px 5px;
	cursor: pointer;
	border: 0px;
	-webkit-transition: ease-in .2s all;
	-o-transition: ease-in .2s all;
	transition: ease-in .2s all;
}
.roundButton:hover{
	background: white !important;
}

.roundButton:nth-child(1){
	background: pink;
}
.roundButton:nth-child(2){
	background: Aquamarine;
}
.roundButton:nth-child(3){
	background: CornflowerBlue ;
}

/* portfolio page*/
.viewFolioBtn{
