/* Range input*/
input[type=range] {
  -webkit-appearance: none;
  margin: 0px 0px 30px 0px;
  width: 40%;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 40%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  -webkit-box-shadow: 1px 1px 1px #000000, 0px 0px 1px #19334d;
          box-shadow: 1px 1px 1px #000000, 0px 0px 1px #19334d;
  background: #66ccff;
  border-radius: 1.3px;
  border: 0.2px solid #19334d;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-box-shadow: 1px 1px 1px #000000, 0px 0px 1px #19334d;
          box-shadow: 1px 1px 1px #000000, 0px 0px 1px #19334d;
  border: 1px solid #19334d;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  background: #ff99cc;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -4px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: #66ccff;
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #19334d;
  background: #66ccff;
  border-radius: 1.3px;
  border: 0.2px solid #19334d;
}
input[type=range]::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #19334d;
  border: 1px solid #19334d;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  background: #ff99cc;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -4px;
}
input[type=range]::-ms-track {
  width: 40%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: #66ccff;
  border: 0.2px solid #19334d;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #19334d;
}
input[type=range]::-ms-fill-upper {
  background: #66ccff;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #19334d;
}
input[type=range]::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #19334d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #ffffff;
  cursor: pointer;
}
input[type=range]:focus::-ms-fill-lower {
  background: #66ccff;
}
input[type=range]:focus::-ms-fill-upper {
  background: #66ccff;
}

/* The square and pseudo classes based cube */
div.square{
	-webkit-transition: .3s all linear;
	-o-transition: .3s all linear;
	transition: .3s all linear;
	width: 30px;
	height: 30px;
	background: #66ccff; /* Old browsers */ /* FF3.6-15 */
	background: -webkit-linear-gradient(-45deg, #66ccff 0%,#ff99cc 100%); /* Chrome10-25,Safari5.1-6 */
	background: -webkit-linear-gradient(315deg, #66ccff 0%,#ff99cc 100%);
	background: -o-linear-gradient(315deg, #66ccff 0%,#ff99cc 100%);
	background: linear-gradient(135deg, #66ccff 0%,#ff99cc 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#66ccff', endColorstr='#ff99cc',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
	display: block;
	position: absolute;
	top: 50px;
	right: 30px;
	-webkit-transform: skewY(170deg);
	    -ms-transform: skewY(170deg);
	        transform: skewY(170deg);
	-webkit-animation: skewAnime infinite 3s ease-in-out;
	        animation: skewAnime infinite 3s ease-in-out;
}
/* An animation for div.square based on the change of skew values*/
@-webkit-keyframes skewAnime{
	0% {-webkit-transform: skewY(170deg);transform: skewY(170deg);}
	50% {-webkit-transform: skewY(190deg);transform: skewY(190deg);}
	100% {-webkit-transform: skewY(170deg);transform: skewY(170deg);}
}
@keyframes skewAnime{
	0% {-webkit-transform: skewY(170deg);transform: skewY(170deg);}
	50% {-webkit-transform: skewY(190deg);transform: skewY(190deg);}
	100% {-webkit-transform: skewY(170deg);transform: skewY(170deg);}
}
div.square::before{
	content:'';
	width: 30px;
	height: 15px;
	background: #66ccff; /* Old browsers */ /* FF3.6-15 */
	background: -webkit-linear-gradient(45deg, #66ccff 0%,#ff99cc 100%); /* Chrome10-25,Safari5.1-6 */
	background: -o-linear-gradient(45deg, #66ccff 0%,#ff99cc 100%);
	background: linear-gradient(45deg, #66ccff 0%,#ff99cc 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#66ccff', endColorstr='#ff99cc',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
	display: inline-block;
	-webkit-transform: skew(-45deg);
	    -ms-transform: skew(-45deg);
	        transform: skew(-45deg);
	position: relative;
	top: -17px;
	left: 7px;
}
div.square::after{
	content:'';
	width: 15px;
	height: 30px;
	display: block;
	background: #ff99cc;
	-webkit-transform: skewY(-45deg);
	    -ms-transform: skewY(-45deg);
	        transform: skewY(-45deg);
	top: -32px;
	left: 30px;	
	position: relative;
}
div.square:hover{
	background: #ff99cc; /* Old browsers */ /* FF3.6-15 */
	background: -webkit-linear-gradient(-45deg, #ff99cc 0%,#66ccff 100%); /* Chrome10-25,Safari5.1-6 */
	background: -webkit-linear-gradient(315deg, #ff99cc 0%,#66ccff 100%);
	background: -o-linear-gradient(315deg, #ff99cc 0%,#66ccff 100%);
	background: linear-gradient(135deg, #ff99cc 0%,#66ccff 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff99cc', endColorstr='#66ccff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}
div.square:hover::before{
	background: #ff99cc; /* Old browsers */ /* FF3.6-15 */
	background: -webkit-linear-gradient(45deg, #ff99cc 0%,#66ccff 100%); /* Chrome10-25,Safari5.1-6 */
	background: -o-linear-gradient(45deg, #ff99cc 0%,#66ccff 100%);
	background: linear-gradient(45deg, #ff99cc 0%,#66ccff 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff99cc', endColorstr='#66ccff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}
div.square:hover::after{
	background: #66ccff;
}



/* Tooltip styling */
[data-tooltip] {
  position: relative;
  z-index: 2;
  cursor: pointer;
}

/* Hide the tooltip content by default */
[data-tooltip]:before,
[data-tooltip]:after {
  visibility: hidden;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  pointer-events: none;
}

/* Position tooltip above the element */
[data-tooltip]:before {
  position: absolute;
  top: 30px;
  left: 50%;
  margin-bottom: 5px;
  margin-left: -80px;
  padding: 7px;
  width: 160px;
  border-radius: 3px;
  background-color: #ff99cc;
  color: #fff;
  content: attr(data-tooltip);
  font-family: Arial;
  text-align: center;
  font-size: 18px;
  line-height: 20px;
}

/* Show tooltip content on hover */
[data-tooltip]:hover:before,
[data-tooltip]:hover:after {
  visibility: visible;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  filter: progid: DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

