@import url('https://fonts.googleapis.com/css?family=Bowlby+One+SC|Patua+One');

body {
	margin: 0;
	/*padding: 15px 0px;*/
	font-family: Arial;
	background: #19334d;
	color: #fff;
	overflow-x: hidden;
}
#container{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	/*optional*/
	-ms-flex-align: center;
	    align-items: center;
}
.container{
	text-align: center;
	padding: 30px 15px 15px 15px;
}
h1{
	font-size: 20px;
	color: #19334d;
	font-family: 'Bowlby One SC';
	-webkit-transform: scaleX(0.9);
	    -ms-transform: scaleX(0.9);
	        transform: scaleX(0.9);
	text-align: center;
	position: absolute;
	left: 0;
	right: 0;
	top: 40px;
	width: auto;
}
@media screen and (max-width: 768px){
	h1{
		font-size: 16px;
	}
	div.square{
		top: 40px;
	}
	#collapseMenuBtn{
		margin: 25px;
	}
}
h2{
	font-size: 18px;
	font-weight: 300;
	text-align: center;
}
h3{
	font-family: Arial;
	text-align: center;
}
h4{
	font-size: 18px;
	margin-bottom: 0px;
	font-family: 'Patua One';
}
#logo{
	width: 170px !important;
	position: absolute;
	top: 100px;
	left: 5px;
	z-index: -1;
}
path.logo{
	-webkit-animation: 3s logo-animation forwards linear infinite;
	        animation: 3s logo-animation forwards linear infinite;
	stroke-dasharray: 105;
	stroke-width: 5px !important;
	fill: transparent;
    stroke: #ff99cc !important;
    width: 100px !important;
    stroke-linecap:round;
	stroke-linejoin:round;
}
@-webkit-keyframes logo-animation{
	0%{
		stroke-dashoffset: 105;
	}
	50%{
		stroke-dashoffset: 0;
	}
	100%{
		stroke-dashoffset: -105;
	}
}
@keyframes logo-animation{
	0%{
		stroke-dashoffset: 105;
	}
	50%{
		stroke-dashoffset: 0;
	}
	100%{
		stroke-dashoffset: -105;
	}
}


